.menuResponsive .menuResponsive-img {
  display: none;
}
.menuResponsive .menuResponsive-menu {
  display: none;
}

.menuResponsive-on {
  display: block;
}

@media screen and (min-width: 1980px) {
  .header-background {
    background-size: 100%;
  }
}
@media screen and (max-width: 1060px) {
  .header-menu {
    justify-content: space-between;
  }
  .header-menu .header-menu-item:not(.menu-item-logo) {
    display: none;
  }

  .menuResponsive {
    position: relative;
  }
  .menuResponsive .menuResponsive-menu {
    background-color: #562121;
    padding: 2rem;
    position: absolute;
    right: 0;
    width: 200px;
    font-size: 1.2rem;
    text-decoration: none;
    border-radius: 5px;
  }
  .menuResponsive .menuResponsive-img {
    display: block;
  }
  .menuResponsive .menuResponsive-img img {
    display: block;
    width: 40px;
    cursor: pointer;
  }
  .menuResponsive .menuResponsive-item:not(:last-child) {
    margin-bottom: 20px;
    padding-bottom: 5px;
  }
  .menuResponsive .menuResponsive-item:not(:last-child):hover {
    border-bottom: 1px solid white;
  }
  .menuResponsive a.menuResponsive-link {
    color: white;
    font-family: "Titillium Web", Verdana, Arial, Helvetica, sans-serif;
    display: block;
    text-decoration: none;
  }

  nav {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding: 0 3rem;
  }

  .header-background {
    height: 300px;
  }
}
@media screen and (max-width: 420px) {
  .header-menu {
    justify-content: space-between;
  }
  .header-menu .header-menu-item:not(.menu-item-logo) {
    display: none;
  }
  .header-menu img {
    width: 100px;
  }
}
